html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1365px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-block;
  padding: 25px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: 2px solid #ffffff;
  user-select: none;
  cursor: pointer;
}
.btn:hover:not([disabled]), .btn:focus:not([disabled]) {
  background-color: #ffffff;
  color: #103aae;
}
.btn:active:not([disabled]) {
  color: #102654;
  background-color: #ffffff;
}
.btn[disabled] {
  opacity: 0.5;
  pointer-events: none;
  cursor: default;
}
@media (max-width: 1365px) {
  .btn--hero {
    padding: 20px;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.logo {
  padding: 10px;
  width: 140px;
  height: 46px;
  flex-shrink: 0;
}
.logo--sitemap {
  display: block;
  margin-top: 50px;
  background-color: #1c3374;
}
@media (max-width: 1365px) {
  .logo {
    width: 131px;
    height: 44px;
  }
}
@media (max-width: 767px) {
  .logo {
    width: 122px;
    height: 42px;
  }
}

.header {
  min-height: 730px;
  background: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
  color: #ffffff;
}
@media (max-width: 1365px) {
  .header {
    min-height: 574px;
    background: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.76%, #73184b 75.5%, #b4022a 100%);
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 350px;
  }
}
@media (max-width: 420px) {
  .header {
    min-height: 461px;
  }
}

.header .container {
  padding: 0 93px;
}
@media (max-width: 1365px) {
  .header .container {
    padding: 0 35px;
  }
}
@media (max-width: 767px) {
  .header .container {
    padding: 0 5px;
  }
}

.header__wrapper {
  max-width: 1180px;
  margin: 0 auto;
}

.header__main-nav {
  margin-top: 23px;
  margin-bottom: 17px;
}
@media (max-width: 1365px) {
  .header__main-nav {
    margin-top: 22px;
    margin-bottom: 18px;
  }
}
@media (max-width: 767px) {
  .header__main-nav {
    margin-top: 7px;
    margin-bottom: 7px;
  }
}

.header__hero {
  margin: 0 auto;
}

.hero {
  max-width: 1160px;
  display: grid;
  grid-template-columns: 33.2% 60.8%;
  gap: 70px;
}
@media (max-width: 1365px) {
  .hero {
    grid-template-columns: 47.8% 47.9%;
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .hero {
    grid-template-columns: 1fr;
  }
}

.hero__wrapper {
  display: grid;
  grid-template-columns: 29.2% 70.8%;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: ".             city" "services-list title" ".             button";
  gap: 0;
  justify-self: end;
  align-items: end;
  text-align: end;
  max-width: 385px;
  padding-bottom: 62px;
  padding-top: 62px;
}
@media (max-width: 1365px) {
  .hero__wrapper {
    max-width: 324px;
    grid-template-columns: 35% 65%;
    padding-bottom: 31px;
    padding-top: 32px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    max-width: 738px;
    margin: 0 auto;
    width: calc(100% - 20px);
    justify-items: end;
    grid-template-columns: 39% 61%;
    padding-top: 34px;
  }
}

.hero__image {
  max-width: 500px;
}
@media (max-width: 1365px) {
  .hero__image {
    max-width: 324px;
  }
}
@media (max-width: 767px) {
  .hero__image {
    display: none;
  }
}

.hero__city {
  grid-area: city;
  margin: 0;
  padding: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 1.62px;
  text-transform: uppercase;
}
@media (max-width: 1365px) {
  .hero__city {
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .hero__city {
    max-width: 290px;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.hero__title {
  grid-area: title;
  padding: 0 0 50px 0;
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 92px;
  font-weight: 800;
  line-height: 1.19;
  text-transform: uppercase;
  letter-spacing: 4.6px;
  word-wrap: break-word;
}
@media (max-width: 1365px) {
  .hero__title {
    font-size: 72px;
    padding-bottom: 49px;
    letter-spacing: 0;
  }
}
@media (max-width: 767px) {
  .hero__title {
    max-width: 310px;
    font-size: 62px;
    letter-spacing: 1.86px;
    padding-top: 14px;
    padding-bottom: 59px;
    line-height: 1.13;
  }
}

.hero__services-list {
  grid-area: services-list;
  padding: 0;
  margin: 12px 0 0 0;
  list-style: none;
  font-size: 14px;
  line-height: 1.5;
  align-self: center;
}
@media (max-width: 1365px) {
  .hero__services-list {
    margin-top: 14px;
  }
}
@media (max-width: 767px) {
  .hero__services-list {
    justify-self: center;
    margin-top: 30px;
  }
}
@media (max-width: 400px) {
  .hero__services-list {
    justify-self: end;
  }
}

.hero__btn {
  grid-area: button;
  margin-left: 42px;
}
@media (max-width: 1365px) {
  .hero__btn {
    margin-left: 26px;
  }
}
@media (max-width: 767px) {
  .hero__btn {
    width: 100%;
    grid-column: 1/-1;
    margin: 0;
  }
}

.main-nav {
  display: flex;
  justify-content: space-between;
}
.main-nav .main-nav__contacts {
  display: flex;
  flex-direction: column;
  max-width: 210px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav .main-nav__contacts {
    flex-direction: row;
    justify-content: end;
    flex-wrap: wrap;
    max-width: 100%;
  }
}
@media (max-width: 1365px) {
  .main-nav .main-nav__contacts {
    margin: 2px 2px 0 0;
  }
}
@media (max-width: 767px) {
  .main-nav .main-nav__contacts {
    margin: 0;
  }
}
.main-nav .main-nav__contacts-link {
  position: relative;
  display: block;
  padding: 7px 10px 13px;
  font-weight: 700;
  font-size: 26px;
  line-height: 1;
  color: #ffffff;
  letter-spacing: 0.2px;
}
.main-nav .main-nav__contacts-link::before {
  position: absolute;
  content: "";
  bottom: 9px;
  right: 10px;
  width: calc(100% - 20px);
  height: 1px;
  background-color: #102654;
  transform: scale(0);
}
@media (max-width: 1365px) {
  .main-nav .main-nav__contacts-link {
    font-size: 24px;
    padding: 6px 9px 11px 14px;
    letter-spacing: 0;
  }
}
@media (max-width: 767px) {
  .main-nav .main-nav__contacts-link {
    font-size: 22px;
    padding: 8px 12px;
  }
}
.main-nav .main-nav__contacts-link:hover:not([disabled]), .main-nav .main-nav__contacts-link:focus:not([disabled]) {
  color: #1c3374;
}
.main-nav .main-nav__contacts-link:active:not([disabled]) {
  color: #102654;
}
.main-nav .main-nav__contacts-link:active:not([disabled])::before {
  transform: scale(1);
}
.main-nav .main-nav__contacts-link[disabled] {
  color: #f8f9fc;
  opacity: 0.5;
  pointer-events: none;
  cursor: default;
}

